html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  width: 100%;
  font-family: $Fuente1;
  color: $Color-claro;
  background-color: $Color-primario;
}

h1 {
  font-size: $h1-size-p;
  line-height: $h1-lh-size-p;
  font-weight: 700;
  color: $Color-secundario;
  text-align: center;
  filter: drop-shadow(1px 1px 12px $Color-secundario);
}
h2 {
  font-size: $h2-size-p;
  line-height: $h2-lh-size-p;
  font-weight: 700;
  color: $Color-secundario;
  margin-block-end: 10px;
}
h3 {
  font-size: $h3-size-p;
  line-height: $h3-lh-size-p;
  font-weight: 600;
}
h4 {
  font-size: $h4-size-p;
  line-height: $h4-lh-size-p;
}
p {
  font-size: $p-size-p;
  line-height: $p-lh-size-p;
}

section {
  max-width: 1300px;
  width: 100%;
  margin: auto;
  padding: 15px 20px;
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

body::-webkit-scrollbar {
  width: 6px;
}
body::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background: $Color-secundario;
}

@media screen and (min-width: 767px) {
  section {
    padding: 15px 30px;
  }
}

@media screen and (min-width: 1024px) {
  h1 {
    font-size: $h1-size-g;
    line-height: $h1-lh-size-g;
  }
  h2 {
    font-size: $h2-size-g;
    line-height: $h2-lh-size-g;
  }
  h3 {
    font-size: $h3-size-g;
    line-height: $h3-lh-size-g;
  }
  h4 {
    font-size: $h4-size-g;
    line-height: $h4-lh-size-g;
  }
  p {
    font-size: $p-size-g;
    line-height: $p-lh-size-g;
  }
  section {
    padding: 15px 40px;
  }
}

@media screen and (min-width: 1240px) {
  section {
    padding: 20px 60px;
  }
}
